export default {
  data() {
    return {
      videoIsEnabled: false,
    };
  },
  methods: {
    enableVideo() {
      this.videoIsEnabled = true;

      this.$nextTick(function () {
        if (window.document) {
          console.log(this.$refs.vid);
          this.$refs.vid.play();
        }
      });
    },
  },
};
